// CSS
.main {
    background: $bg-color-ai; //#070F1D
    height: auto;
}

.btn-sign-in {
	background-color: $color-blue;
	padding: 8px 15px;
	border-radius: 5px;
}

.btn-sign-in:hover {
    color: $color-white;
    background-color: darkblue;
}

.desc-p {
    line-height: 26px;
    color: $color-white;
    text-align: center;
    margin-bottom: 40px;
}

h2 {
    font-weight: $fw-700;
    font-size: $fs-25;
    line-height: 30px;
    color: $color-white;
    text-align: center;
    margin-bottom: 30px;
}

h3 {
    line-height: 20px;
    font-size: 16px;
    color: $color-white;
    margin-bottom: 17px;
}

// header
.header {
    background-color: $bg-color-nav;

    @media (max-width: $width-lg) {
        .menu-nav {
            display: none;
        }

        .input-wrapper-email, 
        .input-wrapper-password {
            width: 100%;
        }

        .reg-wrapper-input {
            width: 100%;

            input[type="email"],
            input[type="password"],
            input[type="name"] {
                width: 225px;
            }
        }
    }

    @media (min-width: $width-lg-max) {
        .navbar {
            display: none;
        }

        .menu-list__item:first-child {
            padding: 0;
        }

        .menu-nav__list {
            display: flex;
            justify-content: space-around;
            align-items: center;
        }

        .menu-list__item.menu-nav__item {
            padding-top: 20px;
        }

        .menu-list__item.menu-nav__item:last-child {
            padding-top: 8px;
        }

        .menu-nav__list_wrapper {
            display: flex;
            align-items: center;
        }

        .wrapper-item {
            margin-right: 40px;
        }

        .wrapper-item:last-child {
            margin-right: 0;
        }

        .menu-list__item.menu-nav__item.wrapper-item:last-child {
            padding-top: 15px;
        }
    }
}

.bg-menu {
    background-color: $bg-color-nav;
}

.menu-list__item {
    margin-bottom: 20px;
}

.menu-list__item:first-child {
    padding-top: 10px;
}

.menu-list__item:last-child {
    height: 52px;
    margin-bottom: 0;
}

.menu-list__link {
    color: $color-white;
    font-weight: $fw-600;
    line-height: 22px;

    &-logo {
        display: flex;
        align-items: center;
    }

    .logo-pic {
        margin-right: 10px;
    }

    &-logo-header {
        color: $color-blue;
    }
}

.btn.btn-primary.btn-style-hover:hover {
    background-color: $color-blue;
    box-shadow: none;
}

.modal-window {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input-wrapper-email,
.input-wrapper-password {
    width: 320px;
}

input[type="email"],
input[type="password"],
input[type="name"] {
    width: 220px;
    height: 30px;
    margin-bottom: 15px;
}

.btn-sign-in-close {
    background-color: grey;
}

.btn-sign-in-close:hover {
    background-color: $bg-color-card;
    color: $color-white;
}

.label-login-email,
.label-login-password,
.label-name,
.label-email,
.label-password {
    font-weight: $fw-600;
}

.reg-wrapper-input {
    width: 355px;
}

// section header
.section-header {
    background-color: $bg-color-header;
    padding-top: 60px;

    @media (min-width: 577px) and (max-width: $width-lg) {
        .section-header__company-items {
            width: 50%;
        }

        .img-bg {
            width: 70%;
        }
    }

    @media (min-width: $width-lg-max) {
        .section-header__elements-wrapper {
            display: flex;
            justify-content: space-between;
            align-items: normal;
            text-align: start;
            margin-bottom: 50px;
        }

        .section-header__btn-link {
            justify-content: start;
        }

        .section-header__elements-wrapper-item {
            width: 50%;
        }

        .section-header__img-bg {
            width: 50%;
        }

        .section-header__company {
            margin-bottom: 80px;
        }

        .resize-item {
            margin-right: 70px;
        }
    }
}

.section-header__elements {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.section-header__header-text_properties-head{
    line-height: 160%;
    font-size: $fs-18;
    text-transform: uppercase;
    color: $color-blue;
    margin-bottom: 10px;
}

h1 {
    font-weight: $fw-800;
    font-size: $fs-40;
    line-height: 130%;
    color: $color-white;
    margin-bottom: 30px;
}

.section-header__desc-text_properties-bottom {
    font-size: $fs-18;
    line-height: 160%;
    color: $color-white;
    margin-bottom: 50px;
}

.section-header__btn-link {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.section-header__btn-link_started,
.section-header__btn-link_video,
.ai__btn-link {
    background-color: $color-blue;
    color: $color-white;
    padding: 15px 18px; // 15px 30px
    border-radius: 5px;
    font-size: 1rem;
    line-height: 27px;
}

.section-header__btn-link_started:hover,
.section-header__btn-link_video:hover,
.ai__btn-link:hover {
    background-color: darkblue;
    color: $color-white;
}

.section-header__btn-link_started {
    margin-right: 20px;
}

.section-header__btn-link_video {
    align-items: center;
    display: flex;
}

.play-video {
    display: flex;
    margin-right: 10px;
}

.img-bg {
    width: 90%; // 100%
    height: auto;
    margin-bottom: 40px;
}

.section-header__company {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.section-header__company-items {
    margin-bottom: 20px;
}

.section-header__company-items_size {
    max-width: 220px;
    height: auto;
}

// section ai

.ai {
    width: 100%;
    height: 870px;
    background-color: $bg-color-ai;
    padding-top: 50px;
    margin-bottom: 70px;

    @media (min-width: 577px) and (max-width: $width-lg) {
        .container-xl {
            .ai__about-img {
                width: 70%;
                height: auto;
            }
        }
    }

    @media (min-width: $width-lg-max) {
        padding-top: 130px;
        height: auto;
        margin-bottom: 170px;

        .ai__wrapper {
            display: flex;
        }

        .ai__elements {
            width: 50%;
            order: 2;

            h2,
            .desc-p {
                text-align: start;
            }

            .ai__btn-link_size {
                margin: 0;
            }
        }

        .ai__about {
            width: 50%;
            order: 1;
        }
    }
}

.ai__elements {
    margin-bottom: 70px;
}

.ai__btn-link_size {
    display: flex;
    width: 165px;
    justify-content: center;
    margin: 0 auto;
}

.ai__about {
    display: flex;
    justify-content: center;
}

.ai__about-img {
    width: 90%;
    height: auto;
}

// section feature

.feature {
    width: 100%;
    height: auto;
    margin-bottom: 100px;

    @media (min-width: 577px) and (max-width: $width-lg) {
        padding-top: 150px;

        .container-xl {

            .carousel-indicators {
                bottom: -10px;
            }

            .feature__card {
                height: 400px;
            }

            .swiper-container {
                display: none;
            }
        }
    }

    @media (max-width: $width-lg) {
        .mySwiper {
            display: none;
        }
    }

    @media (min-width: $width-lg-max) {
        .carousel-indicators {
            button[type="button"]:nth-child(4),
            button[type="button"]:nth-child(5) {
                display: none;
            }
        }

        .carousel {
            display: none;
        }

        .feature__card {
            width: 362px;
            height: auto; //390px
            margin-right: 30px;
        }

        .feature__card:last-child {
            margin-right: 0;
        }

        .swiper-container {
            height: 490px;
        }

        .swiper-wrapper {
            height: 460px;
        }

        .feature__card-img_size {
            margin: 30px auto;
        }

        .feature__text-header {
            width: 90%;
            margin: 0 auto 30px;
        }

        .swiper-pagination-bullet-active {
            background-color: $color-white;
            opacity: 1;
        }

        .swiper-pagination-bullet {
            background-color: $color-white;
        }

        .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
            bottom: 0;
        }
    }

    @media (min-width: $width-xl-max) {
        .swiper-wrapper {
            height: 390px;
        }

        .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
            bottom: 30px;
        }
    }
}

.feature__elements {
    width: 65%;
    margin: 0 auto;
}

.carousel-indicators [data-bs-target] {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 5px;
    margin-left: 5px;
}

.feature__card {
    width: 100%;
    height: 550px;
    background-color: $bg-color-card;
}

.feature__card-img_size {
    width: 73px;
    height: 70px;
    margin: 47px auto;
}

.feature__text-header {
    font-weight: $fw-700;
    font-size: $fs-20;
    line-height: 32px;
    color: $color-white;
    text-align: center;
    margin-bottom: 30px;
}

.feature__text-desc {
    line-height: 26px;
    text-align: center;
    color: $color-white;
    margin-bottom: 30px;
    width: 80%;
    margin: 0 auto;
}

// section about

.about {
    width: 100%;
    height: auto;
    padding-bottom: 100px;

    @media (min-width: 577px) and (max-width: $width-lg) {
        .container-xl {
            .about__item-img {
                width: 70%;
                height: auto;
            }
        }
    }

    @media (min-width: $width-lg-max) {
        .about__wrapper {
            display: flex;
            justify-content: center;
        }

        .about__elements {
            max-width: 550px;

            h2,
            .desc-p {
                text-align: start;
            }
        }
    }
}

.about__item {
    display: flex;
    justify-content: center;
}

.about__item-img {
    width: 90%;
    height: auto;
}

// footer 

.footer {
    width: 100%;
    height: 1150px;
    background-color: $bg-color-nav;
    padding-top: 50px;

    @media (min-width: $width-lg-max) {
        height: 510px;
        .footer__wrapper {
            display: flex;
            justify-content: space-around;
        }

        .footer-nav-list {
            width: 255px;
            text-align: start;
            align-items: flex-start;
        }

        .footer-style {
            text-align: start;
        }

        .footer-wrapper {
            display: -webkit-box;
        }

        .footer-nav-mod {
            width: 165px;
        }
    }
    
    @media (min-width: $width-xl) {

        .footer__wrapper {
            justify-content: center;
        }

        .footer-nav-list {
            width: 284px;
        }

        .footer-nav-mod {
            width: 165px;
        }

        .footer-nav-list:first-child {
            margin-right: 30px;
        }
    }

}

.footer-wrapper {
    display: flex;
    justify-content: space-around;
}

.footer-nav-list_size {
    width: 50%;
}

.footer-nav-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0;
    margin-bottom: 70px;
}

.footer-nav-list__item_margb-20 {
    margin-bottom: 15px;
}

.footer-nav-list__item_indent {
    margin-bottom: $fs-20;
}

.footer-nav-list__link-column_company {
    opacity: 1 !important;
    transition: .15s;
}

.footer-nav-list__link-column_company:hover {
    font-weight: $fw-600;
}


.footer-style {
    font-weight: $fw-400;
    font-size: $fs-14;
    color: $color-white;
    text-align: center; //start
    width: 100%; //320px
}

.footer-nav-list__link {
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-nav-list__info:hover {
    font-weight: $fw-700;
}

.footer-nav-list__link-img {
    width: 14px;
    height: 13px;
    margin-right: 10px;
}

.footer-nav-list__link-img_size {
    height: 16px;
}

.footer-nav-list__link_first-col {
    display: inline-block;
    margin-bottom: 10px;
}

.footer-nav-list__logo-img {
    margin-bottom: 10px;
}

.footer-nav-list__info {
    font-size: $fs-14;
    font-weight: $fw-400;
    line-height: 18px;
    color: $color-white;
    transition: .15s;
}

.footer-nav-list__link-column {
    font-weight: $fw-400;
    font-size: $fs-14;
    line-height: 18px;
    color: $color-white;
    opacity: .6;
}

.footer-nav-list__link-column:hover {
    opacity: 1;
    color: $color-white;
}

.res-hover:hover {
    opacity: .6;
}

.margr-25 {
    margin-right: 15px;
}

.btn-lang {
    background-color: inherit;
    border: none;
}

.btn-check:focus+.btn-primary, 
.btn-primary:focus,
.btn-primary:hover {
    background: inherit;
    border-color: none;
    box-shadow: none;
}

.remove-mb {
    margin-bottom: 0;
}